import { mapMutations } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  methods: {
    ...mapMutations({
      setNavigationIndex: 'SET_NAVIGATION_INDEX'
    }),
    onSkipPageFn (url, id) {
      if (id || id === 0) {
        switch (id) {
          case 0:
            this.setNavigationIndex(0)
            break
          case 2:
            this.setNavigationIndex(2)
            break
          default:
            return false
        }
      }
      uni.switchTab({ url })
    },
    // 关闭弹窗
    onShowStatusFn () {
      this.$emit('popupStatusChild', false)
    },
    scrollBottomFn () {
      uni.pageScrollTo({
        scrollTop: 10000,
        duration: 300
      })
      this.onShowStatusFn()
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    }
  },
  props: {
    obj: {
      type: Object,
      default: {}
    }
  }
}
